import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext";
import { DeleteAccountForm } from "../types";
import "./DeleteAccount.css";

const DeleteAccount: React.FC = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [formData, setFormData] = useState<DeleteAccountForm>({
    reason: "",
    password: "",
  });
  const [isDeleting, setIsDeleting] = useState(false);
  const [errors, setErrors] = useState<Partial<DeleteAccountForm>>({});

  const handleInputChange = (field: keyof DeleteAccountForm, value: string) => {
    setFormData({
      ...formData,
      [field]: value,
    });
    // 
    if (errors[field]) {
      setErrors({
        ...errors,
        [field]: undefined,
      });
    }
  };

  const validateForm = (): boolean => {
    const newErrors: Partial<DeleteAccountForm> = {};

    if (!formData.reason.trim()) {
      newErrors.reason = "";
    }

    if (!formData.password.trim()) {
      newErrors.password = "";
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    setIsDeleting(true);

    try {
      // API
      await new Promise((resolve) => setTimeout(resolve, 1000));

      // 
      console.log("");
      navigate("/login");
    } catch (error) {
      console.error(":", error);
    } finally {
      setIsDeleting(false);
    }
  };

  const handleCancel = () => {
    navigate("/settings");
  };

  return (
    <div className="delete-account">
      <div className="container">
        <div className="edit-form">
          <div className="form-header">
            <h2 className="form-title">Meetolio</h2>
            <p className="form-description">
              
              
            </p>
          </div>

          <form onSubmit={handleSubmit}>
            {/*  */}
            <div className="form-group">
              <label htmlFor="reason"></label>
              <textarea
                id="reason"
                value={formData.reason}
                onChange={(e) => handleInputChange("reason", e.target.value)}
                placeholder=""
                rows={5}
                className={errors.reason ? "error" : ""}
              />
              {errors.reason && (
                <div className="error-message">{errors.reason}</div>
              )}
            </div>

            {/*  */}
            <div className="form-group">
              <label htmlFor="password"></label>
              <input
                type="password"
                id="password"
                value={formData.password}
                onChange={(e) => handleInputChange("password", e.target.value)}
                placeholder=""
                className={errors.password ? "error" : ""}
              />
              {errors.password && (
                <div className="error-message">{errors.password}</div>
              )}
            </div>

            {/*  */}
            <div className="form-group">
              <label></label>
              <div className="warning-section">
                <ul className="warning-list">
                  <li>
                    
                  </li>
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
            </div>

            {/*  */}
            <div className="form-actions">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={handleCancel}
                disabled={isDeleting}
              >
                
              </button>
              <button
                type="submit"
                className="btn btn-danger"
                disabled={isDeleting}
              >
                {isDeleting ? "..." : ""}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default DeleteAccount;
